<template>
  <header class="header">
    <a href="#" class="header__link">
      <img
        src="@/assets/logo.png"
        class="header__img-link"
        alt="main logo"
        width="155"
        height="55"
      />
    </a>
    <nav class="header__nav" :class="{ active }">
      <ul class="header__nav-list">
        <li
          class="header__nav-item"
          v-for="(item, index) in headerMenuItems"
          :key="index"
        >
          <a href="#" class="item-link" :style="`--my-src: url(${item.img})`">{{
            item.itemName
          }}</a>
        </li>
        <a class="list-login" @click="popupMenuOpen">Login</a>
      </ul>
    </nav>
    <div class="header__wrapper-btn-login" @click="popupMenuOpen">
      <div class="header__bg-place">
        <a class="header__link-letter">
          <svg
            width="32"
            height="22"
            viewBox="0 0 32 22"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
            xmlns:xlink="http://www.w3.org/1999/xlink"
          >
            <g filter="url(#filter0_d_1_37)">
              <path
                d="M0 5C0 2.23858 .23858 0 5 0H27C29.7614 0 32 2.23858 32 5V17C32 19.7614 29.7614 22 27 22H5C2.23858 22 0 19.7614 0 17V5Z"
                fill="#FFC728"
              />
              <path
                d="M0 5C0 2.23858 2.23858 0 5 0H27C29.7614 0 32 2.23858 32 5V17C32 19.7614 29.7614 22 27 22H5C2.23858 22 0 19.7614 0 17V5Z"
                fill="url(#pattern0)"
              />
            </g>
            <defs>
              <filter
                id="filter0_d_1_37"
                x="0"
                y="0"
                width="37"
                height="22"
                filterUnits="userSpaceOnUse"
                color-interpolation-filters="sRGB"
              >
                <feColorMatrix
                  type="matrix"
                  values="0 0 0 0 0.22996078 0 0 0 0 0.22678431 0 0 0 0 0.109804 0 0 0 1 0"
                />
                <feBlend
                  mode="normal"
                  in2="BackgroundImageFix"
                  result="effect1_dropShadow_1_37"
                />
                <feBlend
                  mode="normal"
                  in="SourceGraphic"
                  in2="effect1_dropShadow_1_37"
                  result="shape"
                />
              </filter>
              <pattern
                id="pattern0"
                patternContentUnits="objectBoundingBox"
                width="1"
                height="1"
              >
                <use
                  xlink:href="#image0_1_37"
                  transform="translate(0 -0.227273) scale(0.00195312 0.00284091)"
                />
              </pattern>
              <image
                id="image0_1_37"
                width="512"
                height="512"
                xlink:href="data:image/png;base64,"
              />
            </defs>
          </svg>
        </a>
      </div>
      <a class="item-link">Login</a>
    </div>
    <Transition name="modal">
      <PopupLogin v-if="popupMenu.active" @close="popupMenuClose" />
    </Transition>
    <div class="header__wrapper-burger">
      <div
        class="header__burger"
        @click="active = !active"
        :class="active ? 'activeBurger' : ''"
      >
        <span></span>
      </div>
    </div>
  </header>
</template>

<script>
import PopupMenu from "./PopupMenu/PopupMenu.vue";
import { mapActions, mapGetters, mapMutations } from "vuex";
import PopupLogin from "./PopupMenu/PopupLogin.vue";

export default {
  name: "Header",
  data() {
    return {
      active: false,
      menuRegister: "none",
      popMenu: "none",
    };
  },
  computed: mapGetters(["headerMenuItems", "popupMenu", "PROFILE"]),
  components: { PopupMenu, PopupLogin },
  methods: { ...mapMutations(["popupMenuOpen", "popupMenuClose"]) },
};
</script>

<style lang="scss">
.header {
  padding: 15px 125px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #ffffff;
  box-shadow: 0px 1px 8px rgba(83, 109, 122, 0.29);
  border-radius: 5px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 2;
  gap: 30px;
  &::before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: #fff;
    z-index: 2;
  }
  &__link {
    z-index: 2;
    margin: 0 30px 0 0;
    display: inline-block;
    font-size: 0;
  }
  &__nav-list {
    display: flex;
    gap: 55px;
    .list-login {
      display: none;
    }
  }

  &__nav-item {
    z-index: 2;
    &:hover a::before {
      transform: translate(-2px, -2px);
      transition: all 0.5s;
    }
    a {
      position: relative;
      &::before {
        content: "";
        width: 20px;
        height: 22px;
        background-image: var(--my-src);
        background-repeat: no-repeat;
        position: absolute;
        left: -26px;
        top: -2px;
        transform: translate(0px, 0px);
        transition: all 0.5s;
      }
    }
  }
  &__wrapper-btn-login {
    display: flex;
    align-items: center;
    gap: 5px;
    z-index: 2;
    transition: all 0.2s;
    &:hover .header__link-letter {
      cursor: pointer;
      transform: translate(-5px, -5px);
      transition: transform 0.5s;
      color: #158cc7;
    }
    &:hover .item-link {
      cursor: pointer;
      color: #158cc7;
    }
  }
  &__bg-place {
    width: 32px;
    min-height: 22px;
    background-color: #ffc728;
    display: flex;
    border-radius: 5px;
  }
  &__link-letter {
    transition: transform 0.5s;
    font-size: 0;
    display: inline-block;
    svg {
      border-radius: 5px;
    }
  }
}

.v-enter-active,
.v-leave-active {
  transition: opacity 0.5s ease;
}

.v-enter-from,
.v-leave-to {
  opacity: 0;
}
.header__wrapper-burger {
  display: none;
  .header__burger {
    position: relative;
    width: 30px;
    height: 30px;
    z-index: 2;
    &::before,
    &::after {
      content: "";
      background-color: #72727e;
      width: 100%;
      height: 2px;
      position: absolute;
      left: 0;
      transition: all 0.3s;
    }
    &::before {
      top: 3px;
    }
    &::after {
      bottom: 3px;
    }
    & > span {
      position: absolute;
      background-color: #72727e;
      left: 0;
      width: 100%;
      height: 2px;
      top: 14px;
      transition: all 0.5s;
    }
  }
}

@media (max-width: 991.98px) {
  .header {
    padding: 15px 25px;
    gap: 20px;
  }
}
@media (max-width: 767.98px) {
  .header {
    padding: 15px 35px;
    gap: 20px;

    &__nav {
      transition: top 0.3s;
      position: fixed;
      background: white;
      top: -100%;
      left: 0;
      z-index: 0;
      width: 100%;
      border-bottom: 1px solid #b7b7c0;
      .header__nav-list {
        flex-direction: column;
        text-align: center;
        padding: 10px 0;
        margin: 0 0 20px 0;
        li > a {
          font-size: 30px;
          display: block;
          padding-bottom: 24px;
          &:hover {
            color: #9b2b3a;
          }
        }
        .list-login {
          font-family: "Raleway", sans-serif;
          display: block;
          font-size: 30px;
          color: #9b2b3a;
        }
        li {
          margin: 0;
          border-bottom: 1px solid #b7b7c0;
          &:last-child {
            margin: 0;
          }
        }
      }
    }
    .active {
      top: 80px;
    }
    .header__wrapper-burger {
      display: block;
      position: relative;
      width: 30px;
      height: 30px;
      z-index: 2;
      
      .activeBurger {
        & > span {
          transform: scale(0);
        }
        &::before {
          top: 13px;
          transform: rotate(45deg);
        }
        &::after {
          bottom: 15px;
          transform: rotate(-45deg);
        }
      }
    }
  }
  .header__wrapper-btn-login {
    display: none;
  }
}
</style>
